import React from "react";
interface Props {
    width: string | number,
    children: any,
    align?: 'center' | 'right' | 'left'
    tip?: string
}
export default function THFila({ width, children, align, tip }: Props) {
    return <th
        data-class="toolbody" data-html="true" data-tip={tip}
        style={{ paddingTop: '1.25rem', minWidth: width, maxWidth: width, width, textAlign: align }}>
        <span>{children} </span>
    </th>
}
export function THInput({ width, children, align, tip }: Props) {
    return <th
        data-class="toolbody" data-html="true" data-tip={tip}
        style={{ minWidth: width, maxWidth: width, width, textAlign: align }}
    >{children}</th>
}